code[class*="language-"],
pre[class*="language-"] {
	color: #ccc;
	background: none;
	font-family: Exo2-Extra-Light;
	font-size: 1.5vh;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	word-wrap: normal;
	line-height: 1.5;
 
    -moz-tab-size: 4;
	-o-tab-size: 4;
	tab-size: 4;
 
	-webkit-hyphens: none;
	-moz-hyphens: none;
	-ms-hyphens: none;
	hyphens: none;
 
}
 
/* Code blocks */
pre[class*="language-"] {
	padding: 2vh;
    margin: 1vh 0;
	overflow: auto;
}
 
:not(pre) > code[class*="language-"],
pre[class*="language-"] {
    margin: 5vh 5vh 0;
    max-height: 50vh;
	border-radius: 1vh;
	background: #1a2639;
}
 
/* Inline code */
:not(pre) > code[class*="language-"] {
	padding: .1rem;
	border-radius: .3rem;
	white-space: normal;
}
 
.token.comment,
.token.block-comment,
.token.prolog,
.token.doctype,
.token.cdata {
	color: #999;
}
 
.token.punctuation {
	color: #ccc;
}
 
.token.tag,
.token.attr-name,
.token.namespace,
.token.deleted {
	color: #e2777a;
}

.token.function-name {
	color: #6196cc;
}
 
.token.boolean,
.token.number,
.token.function {
	color: #f08d49;
}
 
.token.property,
.token.class-name,
.token.constant,
.token.symbol {
	color: #f8c555;
}
 
.token.selector,
.token.important,
.token.atrule,
.token.keyword,
.token.builtin {
	color: #cc99cd;
}
 
.token.string,
.token.char,
.token.attr-value,
.token.regex,
.token.variable {
	color: #7ec699;
}
 
.token.operator,
.token.entity,
.token.url {
	color: #67cdcc;
}
 
.token.important,
.token.bold {
	font-weight: bold;
}
.token.italic {
    font-style: italic;
}
 
.token.entity {
	cursor: help;
}
 
.token.inserted {
	color: green;
} 
.tech-code-block__title--bold{
	margin: 20vh 5vh 0;
	color: #2d2d2d;
    font-size: 3vh; 
    font-family: Exo2-Bold;
}
/* Tech Bar */
.tech-bar__body{
    margin: 0 4vh 0;
}
.tech-bar__cart-button{
	width: 100%;
	padding: .5vh;
	display: block;
}
.tech-bar__icon{
	width: 5vh;
	margin-left: auto;
	margin-right: auto;
	display: block;
}
.tech-bar__avatar--shadowed{
    width: 17vh;
    height: 17vh;
    margin: 3vh 0 0 0;
    border-radius: 50%;
    display: inline-block;
    box-shadow: 0 0 0.75vh #888888;
}
.tech-bar__avatar-image{
	width: 12vh;
	margin: 2.5vh auto 0 auto;
	display: block;
}
.tech-bar__description--shadowed{
    margin: -12vh 0 0 10vh;
    display: block;
    background-color: #1a2639;
    border-radius: 1vh;
    padding: 3.5vh 3vh 4vh 8.25vh;
    overflow: auto;
    box-shadow: 0 0 0.75vh #888888;
}
.tech-bar__info--extra-light{
    color: #f5f5f5;
    font-size: 2vh;
    font-family: Exo2-Extra-Light;
    text-align: left;
    word-wrap: break-word;
}
@media only screen and (max-width: 800px) {
	.tech-bar__cart-button{
		visibility: hidden;
		display: none;
	}
	.tech-bar__body{
		margin: 0;
	}
	.tech-bar__profile{
		margin: 0 0 -.2rem;
	}
	.tech-bar__avatar--shadowed{
		width: 100%;
		height: 8rem;
		margin: 0;
		border-radius: 0;
		box-shadow: none;		
	}
	.tech-bar__avatar-image{
		width: 6rem;
		margin: 1rem auto 0 auto;
		display: block;
	}
	.tech-bar__description--shadowed{
		margin: 0;
		border-radius: 0;
		padding: 4rem;
		overflow: none;
		box-shadow: none;
	}
	.tech-bar__info--extra-light{
		font-size: 1.25rem;
	}
	code[class*="language-"],
	pre[class*="language-"] {
		font-size: 1rem; 
	}	
	:not(pre) > code[class*="language-"],
	pre[class*="language-"] {
		margin: 0;
		max-height: 25rem;
		border-radius: 0;
	}
	pre[class*="language-"] {
		padding: 0;
		margin: 0;
		overflow: auto;
	}
	.tech-code-block__title--bold{
		margin: 10rem 0 1.5rem 0;
		font-size: 2rem; 
		text-align: center;
	}
}